<template>
  <div id="app">
    <div class="video-container">
      <canvas class="canvas" />
      <video class="video" playsinline :style="{height:videoHeight, width: videoWidth}"/>
      <div class="render">
        <div>{{width}}x{{height}}</div>
        <div class="device" v-for="device in allDevices" :key="device.deviceId">{{device.kind}}: {{device.deviceId.substr(0,9)}}: {{device.label}}</div>
        <div class="icons" :style="{ bottom: startY + 'px' }">
          <a-icon type="thunderbolt" @click="openFlashLight" />
          <a-icon type="camera" @click="takePhoto"/>
          <a-icon type="sync" @click="switchCamera"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, Icon, message, } from 'ant-design-vue';
export default {
  name: 'App',
  components: {
    AButton: Button,
    AIcon: Icon,
  },
  data: function() {
    const { clientHeight: height, clientWidth: width  } = document.body
    return {
      videoDom: undefined,
      allDevices: [],
      startY: 0,
      frontCamera: true,
      width,
      height,
      deviceIds: [],
      usingDeviceId: '',
      videoStream: undefined,
      front: false
    }
  },
  computed: {
    videoHeight: function () {
      return `${this.height}px`;
    },
    videoWidth: function () {
      return `${this.width}px`;
    }
  },
  methods: {
    openCamera() {
      // 老的浏览器可能根本没有实现 mediaDevices，所以我们可以先设置一个空的对象
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }

      // 一些浏览器部分支持 mediaDevices。我们不能直接给对象设置 getUserMedia
      // 因为这样可能会覆盖已有的属性。这里我们只会在没有getUserMedia属性的时候添加它。
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function(constraints) {
          // 首先，如果有getUserMedia的话，就获得它
          const { getUserMedia: _getUserMedia, webkitGetUserMedia, mozGetUserMedia } = navigator;
          const getUserMedia = _getUserMedia || webkitGetUserMedia || mozGetUserMedia;

          // 一些浏览器根本没实现它 - 那么就返回一个error到promise的reject来保持一个统一的接口
          if (!getUserMedia) {
            return Promise.reject(new Error('getUserMedia is not implemented in this browser'));
          }

          // 否则，为老的navigator.getUserMedia方法包裹一个Promise
          return new Promise(function(resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject);
          });
        }
      }
      const self = this;
      navigator.mediaDevices.enumerateDevices().then((res) => {
        self.allDevices = res;
        const deviceIds = res.filter((d) => d.kind === 'videoinput').map(({ deviceId }) => deviceId);
        self.deviceIds = deviceIds
        if (!self.usingDeviceId && deviceIds.length) {
          // 取最后一个
          self.usingDeviceId = deviceIds[deviceIds.length -1];
        }
        // 开始显示图像
        self.playVideo();
      })
    },
    playVideo() {
      const { clientHeight: height, clientWidth: width  } = document.body
      this.height = height;
      this.width = width;
      const { usingDeviceId = '' } = this;
      message.info(usingDeviceId.substr(0,9))
      const facingMode = this.front ? 'user': 'environment'
      const constraints = {
        audio: false,
        video: {
          width,
          height,
          // deviceId: usingDeviceId
          facingMode
        }
      };
      const self = this;
      navigator.mediaDevices.getUserMedia(constraints)
        .then(function(stream){
          const videoDom = document.querySelector('video');
          // 旧的浏览器可能没有srcObject
          if ("srcObject" in videoDom) {
            videoDom.srcObject = stream;
          } else {
            // 防止在新的浏览器里使用它，应为它已经不再支持了
            videoDom.src = window.URL.createObjectURL(stream);
          }
          videoDom.onloadedmetadata = function(e) {
            videoDom.play();
            self.videoDom = videoDom;
            self.videoStream = stream;
          };
        })
        .catch(function(err){message.error(err.name + ": " + err.message); });
    },
    openFlashLight() {
      message.info('打开/关闭闪光灯')
    },
    switchCamera() {
      // const { usingDeviceId, deviceIds, videoStream } = this;
      // // 停止视频流
      // videoStream.getTracks().forEach((track) => {
      //   track.stop();
      // })
      // let curDeviceIndex = deviceIds.findIndex((s) => s === usingDeviceId);
      // const deviceLength = deviceIds.length;
      // if (deviceLength < 2 || curDeviceIndex < 0) {
      //   message.warn(`当前无法切换摄像头`);
      //   return;
      // }
      // curDeviceIndex += 1;
      // const nextDeviceId = deviceIds[curDeviceIndex % deviceLength];
      // if (nextDeviceId) {
      //   this.usingDeviceId = nextDeviceId;
      //   this.playVideo();
      // }
      this.front = !this.front;
      this.playVideo()
    },
    takePhoto() {
      const { videoDom } = this;
      if (videoDom) {
        const canvas = document.createElement("canvas");
        const { clientHeight: height, clientWidth: width  } = document.body
        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(videoDom, 0, 0, canvas.width, canvas.height);
        const base64 = canvas.toDataURL("image/jpeg")
        if (base64) {
          console.info(base64)
          message.success('拍照成功')
        }
      }
    }
  },
  mounted() {
    this.openCamera();
    const { clientHeight: height, clientWidth: width  } = document.body
    const innerWidth = Math.floor(width * 0.68);
    const innerHeight = Math.floor(innerWidth / 2 * 3);
    const startX = Math.floor((width - innerWidth) / 2);
    const startY = Math.floor((height - innerHeight) / 3) ;
    this.width = width;
    this.height = height;
    this.startY = startY;

    const canvas = document.querySelector('canvas')
    canvas.width = width;
    canvas.height = height;
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = 'rgba(255,255,255,0.6)';
    ctx.fillRect(0, 0, width, height);

    ctx.clearRect(startX, startY, innerWidth, innerHeight);
    ctx.strokeStyle = '#87d068';

    const lineHeight = 2;
    const half = lineHeight / 2;

    ctx.lineWidth = lineHeight;
    ctx.strokeRect(startX, startY, innerWidth, innerHeight);
    //
    const cornerWidth = 30;

    // 上边框
    ctx.clearRect(startX + cornerWidth, startY - half, innerWidth - 2 * cornerWidth, lineHeight);
    // 下边框
    ctx.clearRect(startX  + cornerWidth, startY + innerHeight - half, innerWidth - 2 * cornerWidth, lineHeight);

    // 左边框
    ctx.clearRect(startX - half, startY + cornerWidth, lineHeight, innerHeight -2 * cornerWidth);
    // 右边框
    ctx.clearRect(startX + innerWidth - half, startY + cornerWidth, lineHeight, innerHeight -2 * cornerWidth);
  }
}
</script>

<style lang="less">
  #app {
    height: 100%;
    width: 100%;
  }
  .video {
    width: 100%;
    position: absolute;
    z-index: 1;
    &-container {
      position: relative;
      height: 100%;
      width: 100%;
      z-index: 2;
      background: #999;
      .canvas {
        position: absolute;
        z-index: 2;
      }
      .render {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 3;
        .icons {
          position: absolute;
          display: flex;
          align-items: center;
          justify-content: space-around;
          width: 100%;
          font-size: 26px;
          transform: translateY(50%);
        }
        .device {
          word-break: break-all;
        }
      }
    }
  }
</style>
